}

.cardDisplay{
  display: flex;
  justify-content: space-evenly;
}



.playerCard{
  box-shadow: 10px 10px 50px 20px #0ff;
  max-width: 100px;
  min-height: 100px;
  min-width: 100px;
  margin: 15px 0;
  text-align: center;
  background-color: #0ff;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  
   
}

.playerCard img{
  padding: 15px 0;
  
}

button{
  border:none;
  min-width: 100px;
}

.title {
  color: grey;
  font-size: 18px;
}

.alignUsers {
 display: grid;
 align-items: flex-start;
 flex-direction: column;
 
}



.alignUsers button{
  max-width: 100px;
}


.alignPlayerShow{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;

}


.welcomPage{
  min-width: 500px;
  min-height: 800px;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.FontAwesome:hover{
  box-shadow: 10px 10px 50px 20px #0ff;
}



.formSignup{
  box-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #0ff, 0 0 30px #0ff, 0 0 40px #0ff, 0 0 55px #0ff, 0 0 75px #0ff;
  margin: 25px 50px 0 50px;
  padding: 25px 0;
  border-radius: 15px;
}


/* Flipping Cards */
.card-container{
  -webkit-perspective: 150px;
  perspective: 150px;
  background-color: black;
  width: 200px;
  height: 200px
}

.card-container:hover .card{
  -webkit-transform: rotateY( 180deg );
            transform: rotateY( 180deg );
}

.card{
  -webkit-transition: -webkit-transform 1s;
            transition: transform 1s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
            
}

.front, .back{
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  width: 200px;
  height: 200px
}

.back{

  -webkit-transform: rotateY( 180deg );
            transform: rotateY( 180deg );
}

.card{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}